export default {
	data() {
		return {
			login: false,
			user: '',
			password: ''
		};
	},
	methods: {
		//跳转到注册页面
		toRegister: function() {
			uni.navigateTo({
				url: '../register/register'
			});
		},
		getUser() {
			console.log(this.user);
		},
		getPwd() {
			console.log(this.password);
		},
		onlogin() {
			if (this.user && this.password) {
				uniCloud.callFunction({
					name: 'login',
					data: {
						user_id: this.user,
						password: this.password
					},
					success(res) {
						console.log(res);
					}
				});
			} else {
				this.login = true;
			}
		}
	}
};
